//
// _nav.scss
//

.nav-tabs,
.nav-pills {
    > li {
        > a {
            color: $gray-700;
            font-weight: $font-weight-medium;
        }
    }
}

.nav-pills {
    > a {
        color: $gray-700;
        font-weight: $font-weight-medium;
    }
}

.nav-tabs-custom {
    border-bottom: 2px solid $gray-300;

    .nav-item {
        position: relative;
        color: $dark;
        .nav-link {
            border: none;

            &::after {
                content: "";
                background: $primary;
                height: 2px;
                position: absolute;
                width: 100%;
                left: 0;
                bottom: -1px;
                transition: all 250ms ease 0s;
                transform: scale(0);
            }

            &.active {
                color: $primary;
                &:after {
                    transform: scale(1);
                }
            }
        }
    }
}

// vertical nav

.vertical-nav {
    .nav {
        .nav-link {
            padding: 24px 16px;
            text-align: center;
            margin-bottom: 8px;
            .nav-icon {
                font-size: 24px;
            }
        }
    }
}
